.page-heading,
.page-contents {
  display: grid; }

.page-heading {
  grid-template-columns: 1fr;
  grid-template-areas: "heading-caption";
  grid-column-gap: 1rem; }

.heading-caption {
  grid-area: heading-caption; }

.page-contents {
  grid-template-columns: repeat(4, 1fr);
  grid-template-areas: "content-1 content-1 . content-2" "content-3 content-3 content-3 content-3";
  grid-column-gap: 1rem;
  grid-row-gap: 2rem; }

.content-1 {
  grid-area: content-1;
  min-width: 200px;
  display: flex;
  flex-direction: row; }

.content-2 {
  grid-area: content-2;
  min-width: 160px; }

.content-3 {
  grid-area: content-3; }

.product-img {
  width: 100%;
  max-height: 120px; }

.absolute-top {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-height: 120px;
  background: rgba(0, 0, 0, 0.2); }

.filter-select {
  flex: 1; }

@media (max-width: 410px) {
  .page-contents {
    grid-template-columns: 1fr 1fr;
    grid-template-areas: "content-1 ." "content-2 ." "content-3 content-3";
    grid-row-gap: 1rem; } }

@media (max-width: 360px) {
  .page-heading {
    grid-template-columns: 1fr;
    grid-template-areas: "heading-caption" "heading-stat-1"; } }

@media (min-width: 1820px) {
  .page-contents {
    grid-template-columns: repeat(4, 494px);
    grid-template-areas: "content-1 . content-2 . " "content-3 content-3 content-3 .";
    grid-column-gap: 1rem;
    grid-row-gap: 2rem; } }
